<template>
  <dv-border-box-10>
    <div id="center">
      <!--  -->
      <div class="up">
        <div class="physiological-data-container">
          <div
            class="data-card"
            v-for="(item, index) in physiologicalData"
            :key="index"
          >
            <div class="data-title">{{ item.title }}</div>
          </div>
        </div>
      </div>
  
      <!--  -->
      <div class="down">
        <div class="risk-points-container">
          <div
            class="risk-card"
            v-for="(risk, index) in riskPoints"
            :key="index"
          >
            <div class="risk-content">
              <div class="risk-point">{{ risk.riskPoint }}</div>
              <div class="risk-subtitle">{{ risk.index }}</div>
              <div class="risk-advice">
                
                <ul>
                  <li v-for="(suggestion, idx) in risk.suggestions" :key="idx">
                    {{ suggestion }}
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </dv-border-box-10>
  </template>
  
  <script>
  import { requestf,apiConfig } from "../../utils/apiConfig.js";
  export default {
    data() {
      return {
        physiologicalData: [
          { title: '' },
          { title: ''},
          { title: ''},
          { title: ''},
          { title: ''},
          { title: ''}
        ],
        riskPoints: [
          {
            title: '',
            index: "D-"+'0.8'+" mg/L",
            riskPoint: '',
            suggestions: [
              '',
              '',
              '',
              ''
            ]
          },
          {
            title: '',
            index: ""+'2',
            riskPoint: '',
            suggestions: [
              '',
              '',
              ''
            ]
          },
          {
            title: '',
            index: ""+'9.4'+ " mmol/L",
            riskPoint: '',
            suggestions: [
              '',
              '',
              '',
              ''
            ]
          }
        ]
      };
    },
    mounted() {
      requestf(32,(res)=>{
        const data = res;
        this.physiologicalData=data.physiological;
        // 
        this.riskPoints = [
          {
            title: '',
            index: `D-${data.riskIndicators.dDimer} mg/L`,
            riskPoint: '',
            suggestions: data.riskIndicators.dDimerSuggestions
          },
          {
            title: '',
            index: `${data.riskIndicators.nutritionScore}`,
            riskPoint: '',
            suggestions: data.riskIndicators.nutritionSuggestions
          },
          {
            title: '',
            index: `${data.riskIndicators.bloodSugar} mmol/L`,
            riskPoint: '',
            suggestions: data.riskIndicators.bloodSugarSuggestions
          }
        ];
      })
    }
  };
  </script>
  
  <style lang="scss" scoped>
  #center {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    background: linear-gradient(to bottom, #0a1f44, #12264f);
    padding: 10px;
    box-sizing: border-box;
    font-family: 'Microsoft YaHei', sans-serif;
  
    //  - 
    .up {
      .physiological-data-container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 12px;
  
        .data-card {
          background: linear-gradient(145deg, #5c9999, #627cef);
          border-radius: 10px;
          padding: 15px;
          box-shadow: 0 0 10px rgba(73, 105, 117, 0.2);
          text-align: center;
          transition: all 0.3s ease;
  
          &:hover {
            box-shadow: 0 0 20px rgba(0, 255, 255, 0.4);
            transform: translateY(-4px);
          }
  
          .data-title {
            font-size: 28px;
            color: #ffd54f;
            margin: 24px;
          }
        }
      }
    }
  
    //  - 
    .down {
      margin-top: 20px;
  
      .risk-points-container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 12px;
  
        .risk-card {
          background: linear-gradient(180deg, #152F54, #8b87db);
          border-radius: 10px;
          padding: 15px;
          box-shadow: 0 0 10px rgba(255, 0, 0, 0.2);
          height: 220px;
          overflow: hidden;
          transition: all 0.3s ease;
  
          &:hover {
            box-shadow: 0 0 20px rgba(255, 87, 34, 0.4);
            transform: translateY(-4px);
          }
  
          .risk-content {
            color: #ffffff;
            font-size: 14px;
            line-height: 1.5;
  
            .risk-point {
              color: #ff6f61;
              font-weight: bold;
              font-size: 18px;
              margin-bottom: 5px;
            }
  
            .risk-subtitle {
              color: #ffd54f;
              margin-bottom: 5px;
            }
  
            .risk-advice {
              color: #a1d7ff;
  
              ul {
                list-style-type: disc;
                padding-left: 20px;
                margin-top: 8px;
  
                li {
                  font-size: 18px;
                }
              }
            }
          }
        }
      }
    }
  }
  </style>