/**
 * @param {Function} fn 
 * @param {Number} delay 
 */
export function debounce(fn, delay) {
  var timer;
  return function () {
    var context = this;
    var args = arguments;
    clearTimeout(timer);
    timer = setTimeout(function () {
      fn.apply(context, args);
    }, delay);
  };
}

/**
 * @param {date} time 
 * @param {String} fmt   yyyy-MM-ddyyyy-MM-dd HH:mm:ss
 */
export function formatTime(time, fmt) {
  if (!time) return '';
  else {
    const date = new Date(time);
    const o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'H+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds(),
      'q+': Math.floor((date.getMonth() + 3) / 3),
      S: date.getMilliseconds(),
    };
    if (/(y+)/.test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        (date.getFullYear() + '').substr(4 - RegExp.$1.length)
      );
    for (const k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
}

/**
 * 
 * @param {Object} nodeList - 
 * @param {Object} nodeStyle - 
 * @param {number} dateDay - 
 * @returns {Object} - 
 */
export function nodeBuilder(nodeList,nodeStyle,dateDay){
  // 
  const assessmentNodes = [];
  // 
  const assessmentLinks = [];
  // 
  const treatmentNodes = [];
  // 
  const treatmentLinks = [];

  // 
  const assessmentAnchorNode = {
    ...nodeStyle['node100'],
    name: '',
    x:-300+600*dateDay
  };
  assessmentNodes.push(assessmentAnchorNode);

  // 
  const treatmentAnchorNode = {
    ...nodeStyle['node200'],
    name: '',
    x:-100+600*dateDay
  };
  treatmentNodes.push(treatmentAnchorNode);

  // dayn
  const daynData = nodeList[`part${dateDay}`];
  // 
  if (daynData) {
    // 
    for (const [parentKey, parentValue] of Object.entries(daynData)) {
      // 
      const parentNode = parentKey === '' ? 'node010' : 'node020';
      // 
      const childNode = parentKey === '' ? 'node001' : 'node002';
      // 
      const isAssessment = parentKey === '';
      // 
      const targetNodes = isAssessment ? assessmentNodes : treatmentNodes;
      // 
      const targetLinks = isAssessment ? assessmentLinks : treatmentLinks;

      // 
      for (const [childKey, childValue] of Object.entries(parentValue)) {
        // 
        targetNodes.push({
          // 
          ...nodeStyle[parentNode],
          // 
          name: childKey
        });

        // 
        targetLinks.push({
          // 
          source: parentKey,
          // 
          target: childKey,
          // 
          ...nodeStyle.links
        });

        // 
        for (const item of childValue) {
          // 
          targetNodes.push({
            // 
            ...nodeStyle[childNode],
            // 
            name: item
          });

          // 
          targetLinks.push({
            // 
            source: childKey,
            // 
            target: item,
            // 
            ...nodeStyle.links
          });
        }
      }
    }
  }

  // 
  return { 
    assessmentNodes, 
    assessmentLinks, 
    treatmentNodes, 
    treatmentLinks 
  };
}