<template>
  <dv-border-box-10>
    <div id="centerLeft1">
      <div class="bg-color-black">
        <!--  -->
        <div class="d-flex pt-2 pl-2">
          <span>
            <span class="text"><b></b></span>
           
          </span>
          
        </div>

        <!--  -->
        <div class="patient-info">
          <!--  -->
          <div class="avatar-container">
            <img src="../../assets/patient.jpg" alt="" class="patient-avatar" />
          </div>

          <!--  -->
          <div class="info-container">
            <div class="info-text">
              <div class="info-row">
                <div class="column">
                  <p><strong></strong>{{ patient.name }} </p>
                  <p><strong></strong>{{ patient.gender }}</p>
                  <p><strong></strong>{{ patient.attending_physician }}</p>
                  <p><strong></strong>{{ patient.diagnosis }}</p>
                </div>

                <div class="column">
                  <p><strong></strong>{{ patient.age }} </p>
                  <p><strong></strong>{{ patient.hospital_number}}</p>
                  <p><strong></strong>{{ patient.head_nurse }}</p>
                  <p><strong></strong>{{ patient.rehabilitation_doctor }}</p>
                </div>
              </div>
            </div>

            <!--  -->
            <div class="bottom-data">
              <div
                class="item-box mt-2"
                v-for="item in healthData"
                :key="item.text"
              >
                <div class="d-flex">
                  <span class="icon"></span>
                  <dv-digital-flop class="dv-digital-flop" :config="item.number" />
                </div>
                <p class="text">{{ item.text }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </dv-border-box-10>
</template>

<script>
import {requestf,apiConfig} from "../../utils/apiConfig";
let patient =  {
  name: '',
  gender: '',
  attending_physician: '',
  diagnosis: '',
  age: '',
  hospital_number: '',
  head_nurse: '',
  rehabilitation_doctor: '',
  rehab_count: 10,
  physical_recovery: 10,
  completion_rate: 10,
  NIHSS_score: 10
};
export default {
  data() {
    return {
      avatar: "../../assets/patient.webp",
      patient: patient
    };
  },
  computed:{
    healthData(){
      const _ = this.patient;
      console.log(this.patient);
      return [
        {
          number: {
            number: [_.rehab_count],
            toFixed: 0,
            textAlign: "left",
            content: "{nt}",
            style: { fontSize: 24 }
          },
          text: ""
        },
        {
          number: {
            number:[ _.physical_recovery],
            toFixed: 1,
            textAlign: "left",
            content: "{nt}",
            style: { fontSize: 24 }
          },
          text: " (%)"
        },
        {
          number: {
            number: [_.completion_rate],
            toFixed: 1,
            textAlign: "left",
            content: "{nt}",
            style: { fontSize: 24 }
          },
          text: " (%)"
        },
        {
          number: {
            number: [_.NIHSS_score],
            toFixed: 0,
            textAlign: "left",
            // content: "{nt}",
            style: { fontSize: 24 }
          },
          text: "(NIHSS)"
        }
      ]
    }
  },
  mounted: async function () {
    await requestf(31,(res)=>{
      this.patient=res;
    })
  },
};
</script>

<style lang="scss" scoped>
$box-width: 100%;
$box-height:100%;

#centerLeft1 {
  padding: 16px;
  height: $box-height;
  width: $box-width;
  border-radius: 10px;
  
  .bg-color-black {
    height: 90%;
    border-radius: 10px;
  }
  
  .text {
    color: #c3cbde;
    font-size: 20px; /*  20px */
  }
  
  .dv-dec-3 {
    position: relative;
    width: 100px;
    height: 20px;
    top: -3px;
  }

  /*  */
  .patient-info {
    display: flex;
    align-items: center;
    margin-top: 10px;

    /*  */
    .avatar-container {
      margin-right: 50px;
      margin-left: 50px;
    }
    
    .patient-avatar {
      width: 160px; /*  */
      height: 220px; /*  */
      object-fit: cover; /*  */
    }

    /*  */
    .info-container {
      .info-text {
        color: #00bcd4; /*  */
        font-size: 20px;
        line-height: 1.8;
        margin-top: 20;
        margin-bottom: 40px;

        .info-row {
          display: flex;
          justify-content: space-between; /*  */
          margin: 12px 0;
        }

        .column {
          display: flex;
          flex-direction: column;
          align-items: flex-start; /*  */
          width: 68%; /*  */
        }

        p {
          margin: 0;
        }
      }

      .bottom-data {
        margin-top: 10px;
        
        .item-box {
          font-size: 18px;
          float: right;
          position: relative;
          width: 50%;
          color: #d3d6dd;

          .dv-digital-flop {
            width: 120px;
            height: 30px;
          }

          .icon {
            font-size: 20px;
            color: #ffc107;
          }
        }
      }
    }
  }
}
</style>
